.containerDescriptionPremium {
    background-color: #f9f9f9;
}

.containerDescriptionPremiumContent {
    padding-top: 30px;
    max-width: 1570px;
    margin: 0 auto;
    min-height: 700px;
    position: relative;
    z-index: 2;
}

.containerDescriptionPremiumContent:before {
    content: ' ';
    display: block;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: 1;
    opacity: 0.6;
    background-image: url("../../images/premiumDescription.jpg");
    background-repeat: no-repeat;
    background-position: 50% 0;
    background-size: cover;
}

.blockContentPremium {
    margin: 30px auto;
    background-color: white;
    width: 700px;
    min-height: 500px;
    position: relative;
    z-index: 2;
}

.informationPremium {
    margin: 0 auto;
    max-width: 625px;
    padding: 30px 0;
}

.informationPremium h2 {
    font-size: 33px;
}

.informationPremium p {
    font-size: 23px;
}

.informationPremium ul li {
    font-size: 23px;
}

.informationPremium ul li:before {
    position: relative;
    top: 10px;
    content: url("../../images/RedOk-PhotoRoom.png");
}

.blockGetPremium {
    margin: 30px 0 20px 0;
    padding: 20px 0 20px 20px;
    max-width: 540px;
    background-color:#eeeeee;
}

.blockGetPremium button {
    cursor: pointer;
    width: 160px;
    height: 56px;
    font-size: 19px;
    background-color: #fb544e;
    text-align: center;
    color: white;
    border-radius: 10px;
    border: none;
}

.blockGetPremium span{
    font-size: 23px;
    font-weight: bold;
    padding-left: 20px;
}



