.containerContent__reading {
    max-width: 1170px;
    margin: 0 auto;
    background-color: #f1f2f2;
}

.containerContent__reading h2 {
    font-size: 26px;
}

.containerReadingPart {
    background-color: #fcfcfc;
}

.descriptionContainer__flex p {
    font-size: 18px;
}


.descriptionContainer__flex {
    padding-top: 10px;
    display: flex;
    gap: 20px;
}

.descriptionContainer__flex button {
    font-size: 18px;
    border: 5px solid #eeeeee;
    background-color: white;
    padding: 4px 5px 9px 10px;
    border-radius: 10px;
    margin-bottom: 10px;
}


.descriptionContainer__flex button:hover {
    border-color: lightgrey;
}


.readingTitleImage {
    width: 500px;
    border: 10px solid white;
    border-radius: 10px;
}

.containerWithChoiceOfTestFlex {
    display: flex;
    justify-content: space-between;
    gap:10px;
    padding: 0 30px;
    flex-wrap: wrap;
}

.blockWithTitleAndChoiceFlex {
    flex: 0 0 45%;
}

.choiceOfTest {
    padding: 20px 0;
    border-bottom: 1px solid gainsboro;
}

.titleWithTest {
    display: flex;
    justify-content: space-between;
}

.choiceOfTest {
    display: flex;
    justify-content: space-between;
}



.choiceOfTest button {
    padding: 8px 16px;
    height: 45px;
    border: 3px solid gainsboro;
    border-radius: 10px;
    background-color: white;
    cursor: pointer;
    font-size: 22px;
    text-transform: lowercase;
}

.choiceOfTest button:hover {
    border: 3px solid lightgrey;
}

.choiceOfTest a {
    color:red;
    text-decoration: underline;
    font-size: 20px;
    padding-top: 5px;
    cursor: pointer;
}

.choiceOfTest p{
    font-size: 18px;
}


.choiceOfTest div:last-child span {
    margin-right: 5px;
    border-radius: 10px;
    padding: 12px 22px;
    background-color: lightgrey;
}

.blockTestReading {
    border: 6px solid gainsboro;
    border-radius: 10px;
}

.block {
    max-width: 1400px;
    margin: 0 auto;
}

.block h2 {
    padding: 20px 0 20px 35px;
    font-size: 23px;
}

.containerChoice {
    border: 5px solid gainsboro;
    border-radius: 10px;
    background-color: white;
}

.buttonPremium {
    padding: 8px 2px;
    font-size: 18px;
    border: 3px solid gainsboro;
    border-radius: 10px;
    background-color: white;
    margin: 20px 0 20px 30px;
    cursor: pointer;
}

@media (max-width: 768px) {
    .readingTitleImage {
        max-width: 310px;
    }
    .container h2 {
        text-align: center;
        padding: 10px 0 0 0;
    }
    .descriptionContainer__flex {
        flex-wrap: wrap;
    }
    .blockWithTitleAndChoiceFlex {
        flex: 0 0 100%;
    }
    .choiceOfTest button {
        display: none;
    }
}