.containerTitle {
    max-width: 1570px;
    margin: 0 auto;
}
.button__choiceOfLanguage {
    padding: 10px 10px 10px 12px;
    background-color: white;
    border-radius: 10px;
    border: 7px solid gainsboro;
    font-size: 26px;
    width: 330px;
    height: 70px;
    text-align: left;
    margin-top: 40px;
}

.button__choiceOfLanguage:hover {
    background-color: cornsilk;
    cursor: pointer;
    vertical-align: top;
}

.button__choiceOfLanguage.eng::before {
    content: "";
    display: inline-block;
    vertical-align: center;
    height: 100%;
    background-image: url("../../images/EnglishFlag.svg");
    width: 32px;
    height: 22px;
    margin-right: 5px;
    background-repeat: no-repeat;
}
.button__choiceOfLanguage.ger::before {
    content: "";
    display: inline-block;
    vertical-align: center;
    height: 100%;
    background-image: url("../../images/GermanFlag.svg");
    width: 32px;
    height: 22px;
    margin-right: 5px;
    background-repeat: no-repeat;
}

.containerTitleFlex {
    display: flex;
    flex-wrap: wrap;
}

.containerTitleFlex div {
    flex: 0 0 48%;
}

.titlePicture {
    display: block;
    margin: 0 auto;
    max-width: 810px;
    max-height:400px;
}

.containerTitleFlex h1 {
    padding-top: 20px;
    font-size: 30px;
}

.containerTitle h2 {
    padding-top: 15px;
    font-size: 25px;
}
.containerTitleFlex p {
    font-size: 21px;
    padding-top: 20px;
    line-height: 30px;
}

.containerTitleFlex.wrap {
    flex-wrap: wrap;
    justify-content: space-around;
    background-color: #f9f9f9;
}

.containerTitleFlex.wrap div {
    border-bottom:1px solid gainsboro ;
}

.containerTitleFlex.wrap img {
    display: block;
    margin: 70px auto;
    max-width: 560px;
    border: 8px solid gainsboro;
    border-radius: 10px;
}

@media (max-width: 960px) {
    .containerTitleFlex div {
        flex: 0 0 98%;
    }
    .containerTitleFlex.wrap img {
        max-width: 800px;
    }

    .titlePicture {
        display: block;
        margin: 30px auto;
        max-width: 800px;
        max-height: 600px;
    }

}


@media (max-width: 481px) {

    .containerTitleFlex.wrap img {
        max-width: 300px;
    }

    .titlePicture {
        display: block;
        margin: 30px auto;
        max-width: 330px;
        max-height: 600px;
    }
}

