.modal {
    height: 100vh;
    width: 100vw;
    background-color: rgba(0,0,0,0.5);
    position: fixed;
    top:0;
    left:0;
    display: flex;
    align-items: center;
    justify-content: center;
    opacity: 0;
    pointer-events: none;
    transition: 0.5s;
}

.modal.active {
    opacity: 1;
    pointer-events: all;
}

.modalContent {
    padding: 20px;
    border-radius: 12px;
    background-color: white;
    max-width: 550px;
    text-align: center;
}

.blockResults {
    width: 110px;
    height: 120px;
    border-radius: 10px;
    margin: 0 auto;
    background-color: #fb544e;
    color: white;
    border: none;
    margin-top: 75px;
}

.blockResults span {
    font-size: 50px;
    font-weight: bold;
}

.containerBLockResults {
    border-bottom: 3px dotted gainsboro;
}

.containerBLockResults p {
    font-size: 20px;
}

.createAccount {
    max-width: 180px;
    border-radius:10px;
    font-size: 20px;
    color: white;
    margin: 20px auto 0 auto;
    background-color: #fb544e;
    padding: 14px 0;
    cursor: pointer;
}

.blockWithTitlesFlex {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    border-top: 1px solid gainsboro;
    border-bottom: 1px solid gainsboro;
    margin-top: 20px;
}

.blockWithTitlesFlex a {
    font-size: 23px;
    color: red;
    text-decoration: underline;
    cursor: pointer;
}

.blockWithTitlesFlex div {
    flex: 0 0 48%;
    padding: 13px 0;
}

.blockWithTitlesFlex span {
    padding: 15px 20px;
    background-color: #e7e7e7;
    border-radius: 7px;
}



