.dictationContainer {
    max-width: 1050px;
    background-color: #e8e8e8;
    margin: 30px auto;
    border-radius: 10px;
}

.contentDictation.none {
    position: absolute !important;
    top: -9999px !important;
    left: -9999px !important;
}

.dictationBlock {
    max-width: 900px;
    margin: 0 auto;
}

.dictationBlock__audio input {
    -webkit-appearance: none;
    height: 15px;
    border-radius: 5px;
    background: #d3d3d3;
    outline: none;
    opacity: 0.7;
    -webkit-transition: .2s;
    transition: opacity .2s;
    pointer-events: auto;
}

.dictationBlock input::-webkit-slider-thumb {
    -webkit-appearance: none;
    appearance: none;
    width: 25px;
    height: 25px;
    border-radius: 50%;
    background: red;
    cursor: pointer;
    z-index: 100;
}

.contentDictation {
    padding-bottom: 70px;
    border-radius: 10px;
}


.dictationBlock h2 {
    padding-top: 50px;
    padding-bottom: 30px;
    font-size: 30px;
}

.dictationBlock__audio {
    margin-top: 20px;
    display: flex;
    background-color: white;
    max-width: 550px;
    font-size: 23px;
    padding: 18px 20px;
}

.dictationField {
    margin-top: 20px;
    padding-left: 30px;
    height: 200px;
    width: 850px;
    background-color: white;
    color:#8e8186;
    font-size: 19px;
    font-family: monospace;
}

.dictationBlock img {
    margin-right: 8px;
    width: 30px;
    cursor: pointer;
}

.submitDictation {
    margin-top: 15px;
    font-size: 20px ;
    width: 190px;
    height: 50px;
    color:white;
    border: none;
    border-radius: 10px;
    text-align: center;
    background-color: #fb544e;
    font-family: Arial;
}

.dictationWords {
    font-size: 23px;
    padding-left: 10px;
}


.titlesDictation {
    max-width: 1480px;
    margin-top: 100px;
    display: flex;
    justify-content: space-between;
}

.titlesDictation p {
    font-size: 23px;
    color: red;
    text-decoration: underline;
    cursor: pointer;
}

.decor {
    text-decoration: line-through;
    border-bottom: 5px solid red;
    font-size: 23px;
    padding-left: 10px;
}

.germanLetters {
    margin-left: 20px;
    padding: 8px 15px;
    background-color: #fefefe;
    border: 3px solid gainsboro;
    border-radius: 10px;
    cursor: pointer;
    font-size: 25px;
}

.rangeVolume {
    transform: rotate(270deg);
    position: relative;
    bottom: 100px;
    right: 90px;
}

@media (max-width: 900px) {
    .dictationBlock__audio {
        flex-wrap: wrap;
        width: auto;
        display: block;
        margin: 30px auto;
    }

    .blockWithTitleAndChoiceFlex:nth-child(1) {
        display: none;
    }

    .dictationBlock {
        max-width: 400px;
        margin: 0 auto;
    }

    .dictationField {
        max-width: 400px;
        display: block;
        margin: 30px auto;
    }
}

