.containerReadingTest {
    max-width: 1570px;
    background-color: #f9f9f9;
}

.containerReadingTestContent {
    max-width: 1480px;
    margin: 0 auto;
}

.containerContentReading {
    margin-top: 20px;
    border: 5px solid gainsboro;
    border-radius: 10px;
    background-color: white;
}
.testFlexReading {
    padding: 30px 10px;
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
}
.testFlexReading div {
    flex: 0 0 45%;
    border: 3px
}

.variantsAnswersReading {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    gap: 10px;
    padding:20px 0;
}

.variantsAnswersReading div {
    flex: 0 0 45%;
    width: 200px;
    font-size: 20px;
    border-radius: 10px;
    border: 3px solid gainsboro;
    cursor: pointer;
    min-height: 50px;
    padding: 4px 0 0 10px;
    text-align: left;
}

.variantsAnswersReading div:hover {
    border-color: grey;
}


.containerReadingTest {
    margin: 0 auto;
    max-width: 1570px;
}

.numberOfQuestion__readingTest {
    margin-top: 20px;
    text-align: center;
    color: white;
    border-radius: 6px;
    padding: 12px 5px;
    max-width: 120px;
    font-size: 18px;
    background-color: #fb544e;
}

.testFlexReading h2 {
    font-size: 30px;
}

.testFlexReading h3 {
    font-size: 23px;
    padding-top: 10px;
}

.testFlexReading p {
    font-size: 23px;
}

.answerGiven {
    pointer-events: auto;
}

.answerGiven::before {
    background: gainsboro;
    border-radius:50%;
    height: 30px;
    width: 28px;
    line-height: 25px;
    display: inline-block;
    text-align: center;
    margin-right: 6px;
}

.answerGiven:nth-child(1)::before {
    content: "a";
}

.answerGiven:nth-child(2)::before {
    content: "b";
}

.answerGiven:nth-child(3)::before {
    content: "c";
}

.answerGiven:nth-child(4)::before {
    content: "d";
}

.answerGiven:hover:before {
    background: grey;
    color:white;
}

.answerGiven.active {
    pointer-events: none;
}

.ButtonBack {
    margin-top: 20px;
    width: 110px;
    height: 53px;
    font-size: 21px;
    border-radius: 12px;
    border: 3px solid gainsboro;
    background-color: #fafbfb;
}

.ButtonBack:hover {
    border-color: lightgrey;
}

.results {
    display: flex;
}

.results p {
    font-size: 25px;
}

@media (max-width: 860px) {
    .testFlexReading div {
        flex: 0 0 100%;
    }
}