

.btn {
    text-decoration: none;
    border: none;
    padding: 12px 40px;
    font-size: 16px;
    border-radius: 5px;
    box-shadow: 7px 6px 28px 1px rgba(0, 0, 0, 0.24);
    cursor: pointer;
    outline: none;
}


btn:hover {
    opacity: 0.8;
}

.listeningPart h2 {
    font-size: 26px;
}

.listeningPart h3 {
    font-size: 25px;
    padding-top: 15px;
}

.player {
    padding-top: 30px;
    background-color: gainsboro;
    min-height: 250px;
}

.player__content {
    display: flex;
    max-width:500px;
    border-radius: 6px;
    padding: 20px 10px 20px 20px;
    background-color: white;
}

.playSong {
    width: 20px;
    padding-top: 4px;
}

.player__content button {
    background-color: white;
    border: none;
}

.range {
    -webkit-appearance: none;
    height: 15px;
    border-radius: 5px;
    background: #d3d3d3;
    outline: none;
    opacity: 0.7;
    -webkit-transition: .2s;
    transition: opacity .2s;
    pointer-events: auto;
    margin-top: 10px;
    min-width: 200px;
}

.range:hover {
    opacity: 1;
}

.range::-webkit-slider-thumb {
    -webkit-appearance: none;
    appearance: none;
    width: 25px;
    height: 25px;
    border-radius: 50%;
    background: red;
    cursor: pointer;
    z-index: 100;
}

.range::-moz-range-thumb {
    width: 25px;
    height: 25px;
    border-radius: 50%;
    background: #4CAF50;
    cursor: pointer;
}

.rangeBlock {
    background-color: red;
    height: 15px;
    border-radius: 10px;
    position:relative;
    bottom: 22px;
    pointer-events: none
}

.listeningPart {
    margin: 0 auto;
    max-width: 1570px;
    border-radius: 10px;
    border: 10px solid gainsboro;
    background-color: white;
}

.blockListeningTask {
    margin: 0 auto;
    max-width: 1480px;
}

.titleListeningTest {
    padding-left: 20px;
}

.blockListeningTask p {
    font-size: 23px;
}

.containerAudio h2 {
    padding-top: 20px;
}

.allContainer {
    justify-content: space-around;
    display: flex;
    flex-wrap: wrap;
    padding-bottom:30px;
}

.container__content {
    flex: 0 0 48%;
}

.container__content__blocks p {
    margin-top: 20px;
    text-align: center;
    color: white;
    border-radius: 6px;
    padding: 12px 5px;
    max-width: 120px;
    font-size: 18px;
    background-color: #fb544e;
}


.testFlex {
    display: flex;
    flex-wrap: wrap;
}

.answer {
    flex: 0 0 47%;
}


.answer {
    margin-top: 20px;
    margin-left: 10px;
    text-decoration: none;
    min-height: 60px;
    font-size: 23px;
    border-radius: 10px;
    padding: 6px 0 0 5px;
    vertical-align: bottom;
    text-align: left;
    border: 5px solid gainsboro;
    cursor: pointer;
    pointer-events:auto;
}



.answer:hover {
    border-color: lightgrey;
}

.answer:hover .letters {
    background: grey;
    color:white;
}

.answer::before {
    background: gainsboro;
    border-radius:50%;
    height: 30px;
    width: 28px;
    line-height: 25px;
    display: inline-block;
    text-align: center;
    margin-right: 6px;
}

.testFlex div:nth-child(1)::before {
    content: "a";
}

.testFlex div:nth-child(2)::before {
    content: "b";
}

.testFlex div:nth-child(3)::before {
    content: "c";
}

.testFlex div:nth-child(4)::before {
    content: "d";
}

.answer:hover:before {
    background: grey;
    color:white;
}

.answer.active {
    background-color: green;
    pointer-events: none;
}

.range.volume {
    transform: rotate(270deg);
    position: absolute;
    left: 27.1%;
    top: 20%;
    background-color: black;
}



@media (max-width: 960px) {
    .container__content {
        flex: 0 0 80%;
    }
    .answer {
        flex: 0 0 47%;
    }
    .containerTitleFlex div {
        flex: 0 0 98%;
    }
    .containerTitleFlex.wrap img {
        max-width: 800px;
    }

    .titlePicture {
        display: block;
        margin: 30px auto;
        max-width: 800px;
        max-height: 600px;
    }
    .range.volume {
        left: 35%;
    }
}

@media (max-width: 600px) {
    .range.volume {
        left: 50%;
    }
}


@media (max-width: 481px) {
    .container__content {
        flex: 0 0 90%;
    }
    .answer {
        flex: 0 0 90%;
    }
    .player__content {
        flex-wrap: wrap;
    }
    .range {
        max-width: 110px;
    }
    .rangeBlock {
        max-width: 110px;
    }
    .containerTitleFlex.wrap img {
        max-width: 300px;
    }

    .titlePicture {
        display: block;
        margin: 30px auto;
        max-width: 330px;
        max-height: 600px;
    }
    .range.volume {
        left: -12%;
        top: 15%;
    }
    .rangeBlock {
        display: none;
    }
}
